<template>
    <div class="container">
        <h1>Signup</h1>
        <div class="form">
            <md-field>
                <label>Name</label>
                <md-input v-model="name"></md-input>
            </md-field>
        </div>
        <div class="group">
            <router-link to="/signup/step2">
                <button type="button" class="btn line-btn">Confirm</button>
            </router-link>
            <router-link to="/">
                <button type="button" class="btn btn-secondary">Cancel</button>
            </router-link>
        </div>
    </div>
</template>

<script>
export default {
    name: 'step1',
    data(){
        return {
            name:''
        }
    },
    created(){
        this.$ga.page({
            page: '/signup/step1',
            title: 'Step1',
            location: window.location.href
        })
    },
}
</script>

<style lang="scss" scoped>
h1{
    text-align: center;
}
.form{
  margin-top: 100px;
}
.btn{
  display: block;
  width: 100%;
  margin-bottom: 20px;
}
</style>


