<template>
    <div class="container">
        <md-card >
            <md-card-header>
                <md-card-header-text>
                    <div class="md-title">{{name}}</div>
                    <div class="md-subhead">Name</div>
                    <div class="md-title">{{deviceID}}</div>
                    <div class="md-subhead">Device ID</div>
                </md-card-header-text>
            </md-card-header>
        </md-card>
        <div class="group">
            <router-link to="/login/action">
                <button type="button" class="btn line-btn" @click="action('DEPOSIT')">Deposit</button>
            </router-link>
            <router-link to="/login/action">
                <button type="button" class="btn line-btn" @click="action('TRANSFER')">Transfer</button>
            </router-link>
            <router-link to="/login/action">
                <button type="button" class="btn line-btn" @click="action('LOAN')">Loan</button>
            </router-link>

            <router-link to="/" class="logout">
                <button type="button" class="btn btn-secondary logout">Logout</button>
            </router-link>
        </div>
    </div>
</template>

<script>
export default {
    name: 'mainScreen',
    data(){
        return{
            name: '',
            deviceID: ''
        }
    },
    created(){
        this.$ga.page({
            page: '/login/main',
            title: 'Main',
            location: window.location.href
        })
    },
    mounted(){
        const vm = this;
        vm.name = vm.$cookieStore.getCookie('NAME'); 
        vm.deviceID = vm.$cookieStore.getCookie('DEVICE_ID');
    },
    methods:{
        action(value){
            const vm = this;
            var tempNum = Number(vm.$cookieStore.getCookie(value));
            vm.$cookieStore.setCookie(value, tempNum+1);   
        }
    }
}
</script>

<style lang="scss" scoped>
.form{
  margin-top: 50px;
}
.group{
    margin-top: 70px;
}
.btn{
  display: block;
  width: 100%;
  margin-bottom: 20px;
}

.logout{
    margin-top: 100px;
}
</style>


