<template>
    <div class="container">
        <div class="form">
            <md-card class="md-primary" md-theme="green-card">
                <md-card-header>
                    <md-card-header-text>
                        <div class="md-title">{{name}}</div>
                        <div class="md-subhead">Name</div>
                        <div class="md-title">{{deviceID}}</div>
                        <div class="md-subhead">Device ID</div>
                    </md-card-header-text>
                </md-card-header>
            </md-card>
            <hr>
            <h2>{{context}}</h2>
            <div class="group">
                <router-link to="/login/finish">
                    <button type="button" class="btn line-btn" @click="save">Yes</button>
                </router-link>
                <router-link to="/login/main">
                    <button type="button" class="btn btn-secondary">No</button>
                </router-link>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'aciton',
    data(){
        return{
            name: '',
            deviceID: '',
            action: '',
            context: ''
        }
    },
    created(){
        const vm = this;

        this.$ga.page({
            page: '/login/action',
            title: 'Action',
            location: window.location.href
        })

        vm.name = vm.$cookieStore.getCookie('NAME');
        vm.deviceID = vm.$cookieStore.getCookie('DEVICE_ID');
        vm.action = vm.$cookieStore.getCookie('ACTION');

        vm.action == 'Deposit' ? vm.context = '是否要申請定存？' : 
            vm.action == 'Transfer' ? vm.context = '是否要轉帳？' : vm.context = '是否要申請貸款？'
    },
    methods:{
        save(){

        }
    }
}
</script>

<style lang="scss" scoped>
.md-title:last-child{
    margin-top: 20px;
}

h2{
    text-align: center;
    margin-top: 50px;
}
.group{
    margin-top: 30px;
    .btn{
        display: block;
        width: 100%;
        margin-bottom: 20px;
    }
}
</style>


