<template>
    <div class="container">
        
        <h1>FINISH</h1>

        <div class="group">
            <router-link to="/">
                <button type="button" class="btn btn-outline-light">Back To Index</button>
            </router-link>
        </div>
    </div>
</template>

<script>
export default {
    name: 'step2',
    created(){
        this.$ga.page({
            page: '/signup/step2',
            title: 'Step2',
            location: window.location.href
        })
    },
}
</script>

<style lang="scss" scoped>
.container{
    background: rgb(37,200,117);
    color: #fff;
    text-align: center;
    h1{
        height: 50px;
        margin-top: 200px;
    }
}

.group{
    margin-top: 50px;
}
</style>


