<template>
  <div class="container">
    <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css">
    <h1>LIME</h1>
    <div class="form">
      <md-field>
        <label>Name</label>
        <md-input v-model="name"></md-input>
      </md-field>
      <md-field>
        <label>Device ID</label>
        <md-input v-model="deviceID"></md-input>
      </md-field>
      <div class="interest">
        <span class="title">興趣偏好</span>
        <span v-if="behavior.deposit > 0" class="badge badge-pill badge-warning">Deposit  {{behavior.deposit}}</span>
        <span v-if="behavior.transfer > 0" class="badge badge-pill badge-warning">Transfer  {{behavior.transfer}}</span>
        <span v-if="behavior.loan > 0" class="badge badge-pill badge-warning">Loan  {{behavior.loan}}</span>
      </div>
    </div>
    <div class="group">
        <router-link to="/login/main">
          <button type="button" class="btn line-btn" @click="login(name, deviceID)">Login</button>
        </router-link>
        <router-link to="/signup/step1">
          <button type="button" class="btn btn-secondary">Sign up</button>
        </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Index',
  data () {
    return {
      name: '',
      deviceID: '',
      behavior: {
        deposit: 0,
        transfer: 0,
        loan: 0
      },

    }
  },
  created(){
    this.$ga.page({
      page: '/index',
      title: 'Home page',
      location: window.location.href
    })
  },
  mounted(){
    const vm = this;
    !vm.$cookieStore.getCookie('DEPOSIT') ? vm.$cookieStore.setCookie('DEPOSIT', 0) : vm.behavior.deposit = vm.$cookieStore.getCookie('DEPOSIT');
    !vm.$cookieStore.getCookie('TRANSFER') ? vm.$cookieStore.setCookie('TRANSFER', 0) : vm.behavior.transfer = vm.$cookieStore.getCookie('TRANSFER');
    !vm.$cookieStore.getCookie('LOAN') ? vm.$cookieStore.setCookie('LOAN', 0) : vm.behavior.loan = vm.$cookieStore.getCookie('LOAN');
  },
  methods:{
    login(name, id){
      const vm = this;
      vm.$cookieStore.setCookie('NAME', name);
      vm.$cookieStore.setCookie('DEVICE_ID', id);
    }
  }
}
</script>

<style lang="scss" scoped>
h1{
  text-align: center; 
  color: rgb(37,200,117);
  font-weight: bold;
}
.form{
  margin-top: 50px;
}
.interest{
  margin-top: 30px;
  .title{
    margin-right: 20px;
  }
}
.btn{
  display: block;
  width: 100%;
  margin-bottom: 20px;
  margin-top: 30px;
}
</style>



