<template>
    <div class="container">
        <md-card>
            <md-card-header>
                <md-card-header-text>
                    <div class="md-title">{{name}}</div>
                    <div class="md-subhead">Name</div>
                    <div class="md-title">{{deviceID}}</div>
                    <div class="md-subhead">Device ID</div>
                </md-card-header-text>
            </md-card-header>
        </md-card>
        <h1>FINISH</h1>

        <div class="group">
            <router-link to="/login/main">
                <button type="button" class="btn btn-outline-light">Back To Main Page</button>
            </router-link>
        </div>
    </div>
</template>

<script>
export default {
    name: 'finish',
    data(){
        return{
            name: '',
            deviceID: ''
        }
    },
    created(){
        this.$ga.page({
            page: '/login/finish',
            title: 'Finish',
            location: window.location.href
        })
    },
    mounted(){
        const vm = this;
        vm.name = vm.$cookieStore.getCookie('NAME'); 
        vm.deviceID = vm.$cookieStore.getCookie('DEVICE_ID');
    },
}
</script>

<style lang="scss" scoped>
.container{
    background: rgb(37,200,117);
    color: #fff;
    text-align: center;
    h1{
        height: 50px;
        margin-top: 100px;
    }
}
.md-card{
    background: #fff;
    color: #000;
    text-align: left;
}
.group{
    margin-top: 50px;
}
</style>


