import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'

import step1 from '@/components/signUp/step1'
import step2 from '@/components/signUp/step2'

import main from '@/components/login/main'
import action from '@/components/login/action'
import finish from '@/components/login/finish'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/signup/step1',
      name: 'step1',
      component: step1
    },
    {
      path: '/signup/step2',
      name: 'step2',
      component: step2
    },
    {
      path: '/login/main',
      name: 'main',
      component: main
    },
    {
      path: '/login/action',
      name: 'action',
      component: action
    },
    {
      path: '/login/finish',
      name: 'finish',
      component: finish
    }
  ]
})



